#navbar-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 70%;
}

#navbar-main-logo {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 25%;
  /* top: 140px; */
  /* top: 5%; */
}

#navbar-main-logo-img {
  clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
  visibility: visible;
  transition: all 800ms;
  transition-delay: 1000ms;
}

#navbar-main-menus-logo {
  position: relative;
  left: 50px;
  bottom: 20px;
  clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  visibility: hidden;
  transition: all 300ms;
  font-size: 23px;
  color: #f2c94c;
}

#navbar-main-logo-text {
  position: relative;

  width: 90%;
  margin: 0 auto;
  text-align: center;
  top: 20px;

  font-size: 23px;
  clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
  visibility: visible;
  transition: all 300ms;
  transition-delay: 1000ms;
  color: #f2c94c;
}

#navbar-main-menus {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  top: 40px;
  color: #ffffff;
  font-size: 30px;
}

#navbar-main-menus > ul > li {
  float: left;
  cursor: pointer;
  margin-right: 40px;
}

