/* navbar-menuActivate Btn */

button {
  border: 0;
}

#navbar-menuBtn,
#navbar-menuBtn-active {
  display: none;
  position: absolute;
  top: 50px;
  right: 10%;
  /* z-index: 91; */
  width: 34px;
  height: 24px;
  background: transparent;
  font-size: 0px;
  transition: all 0.3s ease 0s;
}

#navbar-menuBtn > i {
  display: block;
  position: absolute;
  left: 0px;
  width: 100%;
  height: 2px;
  background: #fff;
}

#navbar-menuBtn > i:nth-of-type(1) {
  top: 0px;
  transition: all 0.3s ease;
  transition-property: top, transform;
  transition-delay: 0.3s, 0s;
}

#navbar-menuBtn > i:nth-of-type(2) {
  top: 50%;
  margin-top: -1px;
  transition: background 0.3s ease 0s;
}

#navbar-menuBtn > i:nth-of-type(3) {
  top: 22px;
  transition: all 0.3s ease;
  transition-property: top, transform;
  transition-delay: 0.3s, 0s;
}

/* navbar-menu-active */

#navbar-menuBtn-active > i {
  display: block;
  position: absolute;
  left: 0px;
  width: 100%;
  height: 2px;
  background: #fff;
}

#navbar-menuBtn-active > i:nth-of-type(1) {
  top: 10px;
  transform: rotate(-45deg);
  transition: all 0.3s ease;
  transition-property: transform, top;
  transition-delay: 0.3s, 0s;
}

#navbar-menuBtn-active > i:nth-of-type(2) {
  display: none;
}

#navbar-menuBtn-active > i:nth-of-type(3) {
  top: 10px;
  transform: rotate(45deg);
  transition: all 0.3s ease;
  transition-property: transform, top;
  transition-delay: 0.3s, 0s;
}

/* navbar-menuBtn-activate */

#navbar-menuBtn-activate {
  opacity: 0;
  position: fixed;
  left: 0;
  top: 0px !important;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #1b1b1b;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#navbar-menuBtn-activate > ul > li {
  font-size: 30px;
  margin-bottom: 50px;
  cursor: pointer;
}

#navbar-menuBtn-activate > img {
  position: relative;
  top: 50px;
}
