@media (max-width: 800px) {
  #navbar-main-menus,
  #navbar-others-menus {
    visibility: hidden;
    opacity: 0;
  }
  #navbar-menuBtn,
  #navbar-menuBtn-active {
    display: block;
    z-index: 2;
  }

  #navbar-main-logo-img,
  #navbar-others-logo-img {
    width: 50vw;
    height: 30vh;
  }

  #navbar-main-logo-text,
  #navbar-others-logo-text {
    position: relative;
    bottom: -15px;
    width: 100%;
    /* text-align: center; */
    font-size: 1.5rem;
  }
}
